                  /* Login/Logout Form */
.container {
  max-width: 400px;
  margin: 50px auto;
  background-color:gray;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="number"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

input[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color:lightgray;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

input[type="submit"]:hover {
  background-color:black;
}

                       /* ContactUS Form */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  /* background-color:rgb(212, 212, 212); */
  ;
}
.contact_container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 120vh;
}
#sign_contact{
  height: 800px;
}

.containers {
  background-color:gray;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  width: 600px;
  display: flex;
  gap: 20px;
}

.info-section {
  flex: 1;
}

.info-section h2 {
  margin: 0 0 20px;
  font-size: 24px;
}

.info-section label,
.info-section p {
  margin-bottom: 5px;
  display: block;
  margin-left: 20px;
}

.info-section input,
.info-section textarea {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
}

.info-section textarea {
  resize: vertical;
}

.info-section button {
  background-color:lightgray;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  width: 250px;
}

.info-section button:hover {
  background-color: black;
}
.info-item{
  display: flex;
  margin-top: 60px;
}
.large-icon {
  font-size: 1.5rem; /* Adjust the size as needed */
}
.large-icons {
  font-size: 3.5rem; /* Adjust the size as needed */
}
